* {
    margin: 0;
    box-sizing: border-box;
  }
  
  .AireLocal-wrapper {
    height: 100vh;
    width: 100%;
    scroll-snap-type: y mandatory;
    overflow-y: scroll;
  }

  .AireLocal-wrapper h1 {
    color: #adefd1ff;
    font-size: 4.5em;
    margin: 0.4em;
  }
  .DappConnector-wrapper-mobile {
    height: 100vh;
    width: 100%;
    scroll-snap-type: y mandatory;
    overflow-y: scroll;
    background-color: #00203fff;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .DappConnector-wrapper-mobile h1 {
    color: #adefd1ff;
    font-size: 2em;
    margin: 0.4em;
  }
  .first-section-project {
    scroll-snap-align: start;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .first-section-column {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    width: 100%;
    position: relative;
    background-color: #00203fff;
  }
  
  /* ------------------------------------ SECOND SECTION ----------------------------------------- */
  .second-section-project-airelocal-airelocal {
    scroll-snap-align: start;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: left;
    padding: 1% 20% 1% 5%;
    background-color: #00203fff;
  }
  
  .second-section-project-airelocal-airelocal h1 {
    color: #adefd1ff;
    font-size: 3em;
    margin: 0.4em 0px;
  }
  
  .MyRole-container {
    display: flex;
    flex-direction: row;
  }
  
  #MyRole-image-AireLocal {
    width: 400px;
    height: 200px;
    margin-top: 12%;
    margin-left: 5%;
    border-radius: 5px;
  }
  
  #Technologies-image-airelocal {
    width: 200px;
    height: 180px;
    margin-left: 10%;

  }
  
  .MyRole-text-container h1 {
    color: #adefd1ff;
    font-size: 1.66em;
    margin: 0.4em 0px;
  }
  
  .second-section-project-airelocal-airelocal div {
    font-size: 1.3em;
    color: white;
  }
  
  li {
    margin: 10px 0px;
  }
  
  li a {
    color: white;
  }
  li a:hover {
    color: #adefd1ff;
  }
  
  .ul-wrapper {
    display: flex;
    flex-direction: row;
  }
  /* ------------------------------------ Go back homme ----------------------------------------- */
  .go-back {
    position: fixed;
    top: 0;
    left: 0;
    width: auto;
    height: auto;
    color: #00203fff;
    display: flex;
    flex-direction: row;
    align-items: center;
    background-color: #adefd1ff;
    padding: 10px;
    border-radius: 10px;
    margin-left: 10px;
    margin-top: 10px;
    font-weight: bold;
    font-size: 1.3em;
    box-shadow: 7px 6px 28px 1px rgba(98, 214, 185, 0.2);
    cursor: pointer;
    transition: all 0.7s ease;
  }
  .go-back:hover {
    transform: translateY(+5px);
    box-shadow: 7px 6px 28px 1px rgba(98, 214, 185, 0.35);
  }
  .go-back:active {
    transform: scale(0.9);
    /* Scaling button to 0.98 to its original size */
    box-shadow: 3px 2px 22px 1px rgba(98, 214, 185, 0.14);
    /* Lowering the shadow */
  }
  
  .go-back img {
    width: 30px;
    height: 30px;
    margin-left: 30px;
  }
  
  @media (max-width:1800px) {
    .AireLocal-wrapper {
      font-size: 0.8em;
    }
  }

  @media (max-width:1400px) {
    .AireLocal-wrapper {
      font-size: 0.7em;
    }
  }

  @media (max-width:1200px) {
    .AireLocal-wrapper {
      font-size: 0.6em;
    }
  }

  @media (max-width:1000px) {

    .second-section-project-airelocal {
      padding: 1% 15% 1% 5%;
    }

    .AireLocal-wrapper {
      font-size: 0.6em;
    }
  }

  @media (max-width:800px) {

    .second-section-project-airelocal {
      padding: 1% 5% 1% 5%;
    }

    .AireLocal-wrapper {
      font-size: 0.55em;
    }
  }